.nav-bar {
  display: none;
}

.nav-bar {
  height: 22px;
  background: -webkit-linear-gradient(45deg, #188FFF 0%, #0036B0 100%);
  background: -o-linear-gradient(45deg, #188FFF 0%, #0036B0 100%);
  background: linear-gradient(45deg, #188FFF 0%, #0036B0 100%);
  color: white;
  position: relative;
  padding: 8px;
}

.nav-bar span {
  position: absolute;
  left: 0;
  top: 0;
  color: white;
  height: 100%;
  font-size: 18px;
  background: none;
  border: none;
}

.nav-bar span img {
  height: 100%;
}

.nav-bar h3 {
  margin: 0;
  font-size: 15px;
}

.nameAndRec--mobile {
  display: none;
}

.nameAndRec--desktop {
  display: block;
}

@media screen and (max-width: 767px)  {
  .totalViewContainer {
    display: block !important;
    min-width: 355px;
  }
  .mainGameArea {
    display: none;
  }

  .totalViewContainer .gamesSideBar {
    display: none;
  }

  .totalViewContainer .liveData .top {
    display: block;
  }

  .totalViewContainer .liveData .top-left,
  .totalViewContainer .liveData .top-right {
    min-width: 0;
    padding: 0;
    max-width: none;
  }

  .totalViewContainer .liveData .scoringRow>h4 {
    padding: 0;
    min-width: 35px;
  }

  .totalViewContainer .rinkMap {
    padding: 2px;
  }

  .mainGameArea.mobileActive {
    display: block;
  }

  .gamesSideBar.mobileActive {
    display: -ms-flexbox;
    display: flex;
  }

  .nav-bar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .gamesSideBar {
    padding: 0 !important;
  }

  .gamesSideBar,.gamesScroll,.disclaimer {
    width: 100% !important;
    max-height: 100% !important;
  }

  .totalViewContainer .gamesScroll {
    margin-top:0;
  }

  .gamesScroll h3 {
    height: 30px;
    line-height: 30px;
  }

  .bannerGroup {
    position: absolute;
    top:0;
    width: 50%;
    height: 48px;
  }

  .bannerGroup.home {
    left: 0;
  }

  .bannerGroup.away {
    right: 0;
  }

  .bannerContainer .bannerGroup img {
    height: 35px;
    width: 35px;
  }

  .bannerContainer .bannerGroup .bannerLabel {
    font-size: 8px;
    margin: 3px;
  }

  .timeAndScore {
    margin-top:48px;
    width: 100%;
    -ms-flex: auto !important;
        flex: auto !important;
    background: rgba(0,0,0,0.9) !important;
  }

  .bannerContainer .bannerGroup h2 {
    line-height: 48px;
    font-size: 28px;
    padding: 0 4px;
  }

  .bannerContainer .bannerGroup .logoPPBadge {
    padding: 2px;
    font-size: 13px;
  }

  .bannerGroup h1 {
    /* display: none; */
  }

  .teamRecord {
    display: none;
  }

  .bannerContainer {
    height: auto !important;
  }

  .totalViewContainer .liveData .scoreData {
    display: block;
  }

  .totalViewContainer .liveData .assistData {
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    /* flex-wrap: nowrap; */
  }

  .totalViewContainer .liveData .assistData .assistOne,
  .totalViewContainer .liveData .assistData .assistTwo {
    padding-left: 0;
    font-size: 0.7em;
    width: auto;
  }

  .scoringRow img {
    margin: 0;
  }

  .scoringTable .scoringRow {
    height: auto;
  }

  .liveData .section-title {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 30px;
  }

  .liveData .latestPlays .eventRow,
  .liveData .scoringTable .scoringRow,
  .eventRow, .scoringRow {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .totalViewContainer .scoringRowScore span {
    min-width: 20px;
  }

  .liveData .buttonRow button {
    height:30px;
    font-size: 13px;
    width: 40%;
    border-radius: 3px;
    border-right: 1px solid #d2d2d2;
  }

  .liveData .buttonRow {
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
  }

  .__react_component_tooltip {
    display: none;
  }

  .liveData .gameRecap .description {
    display: none;
  }

  .liveData .blurb {
    position: relative !important;
    background: none !important;
    color: #262626;
  }

  .gameRecap img {
    display: none;
  }

  .liveData .gamePreview .description {
    position: relative;
  }

  .liveData .gameRecap h1, .liveData .gamePreview h1 {
    font-size: 15px;
  }

  .liveData .nameAndRec--mobile {
    display: block;
    font-size: 15px;
  }

  .nameAndRec--desktop {
    display: none;
  }
}



